.banner {
    height: 80vh;
    color: white;
}

.banner-content {
    margin-left: 30px;
    padding-top: 300px;
}

.b_title {
    padding-bottom: 0.3rem;
    font-weight: 800;
    font-size: 3rem;
}

.banner-description {
    width: 45rem;
    line-height: 1;
    padding: 1rem 0;
    font-size: 1rem;
    max-width: 360px;
    height: 80px;
}

.banner-b {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-size: 1.2rem;
    font-weight: 700;
    border-radius: 0.2vw;
    padding: 1rem 3rem;
    margin-right: 2rem;
    background-color: rgba(51, 51, 51, 0.5);
}

.play {
    color: #000;
    background-color: #fff;
}

.banner_fadeBottom {
    height: 40vh;
    background-image: linear-gradient(180deg, transparent, rgb(37, 37, 37, 0.91), #111);
}
